.review-score {
    display: inline-block;
    width: 60px;
    height: 60px;
    line-height: 60px;
    color: #fff;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px dashed rgba(255, 255, 255, 0.4);
    border-radius: 50%;
}

.positive-score{border:1px dashed rgba(8, 184, 2, 0.50) !important;} 
 
.middle-score{border:1px dashed rgba(255, 158, 12, 0.5) !important;} 
 
.negative-score{border:1px dashed rgba(184, 2, 2, 0.5) !important;} 
 
.unknown-score{border:1px dashed rgba(53, 53, 53, 0.5) !important;} 