/* Embed Player */
.embed {
    background-color: #2a2b2d;
  }
  
  .embed-icon i, .embed::after {
    position: relative;
    z-index: 3;
    display: inline-block;
    width: 5.625rem;
    height: 5.625rem;
    font-size: 28px;
    line-height: 5.625rem;
    color: #343336;
    text-align: center;
    cursor: pointer;
    content: "\E03F";
    background-color: #fff;
    box-shadow: 0 1px 25px 0 rgba(0, 0, 0, 0.2);
    border-radius: 50%;
    transition: all 0.2s ease-in-out;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .embed-icon i, .embed::after {
      transition: none;
    }
  }
  
  @media (max-width: 575.98px) {
    .embed-icon i, .embed::after {
      width: 5.11364rem;
      height: 5.11364rem;
      font-size: 25px;
      line-height: 5.11364rem;
    }
  }
  
  .embed-icon:hover i, .embed:hover::after {
    box-shadow: 0 1px 25px 0 rgba(0, 0, 0, 0.2);
    opacity: .85;
  }
  
  .embed.play::after {
    -webkit-animation-name: zoomOut;
    animation-name: zoomOut;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }
  
  .embed-title {
    position: absolute;
    top: 25px;
    left: 30px;
    z-index: 3;
    padding: 10px 18px;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    background-color: #2a2b2d;
    border-left: 3px solid #2575dc;
    border-radius: 30px;
  }
  
  @media (max-width: 575.98px) {
    .embed-title {
      display: none;
    }
  }
  
  .embed-responsive {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .embed-icon {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    text-align: center;
  }
  
  .embed-icon i::before {
    margin-right: -2px;
  }
  
  .embed-icon i-dark i {
    color: #fff;
    background-color: #1e1e20;
  }
  
  .embed-icon-dark::after {
    color: #fff;
    background-color: #1e1e20;
  }

.embed-player .embed-icon {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    text-align: center;
  }
  
  .embed-player .embed-icon i {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 75px;
    height: 75px;
    font-size: 22px;
    color: #343336;
    cursor: pointer;
    background-color: #fff;
    transition: all 0.2s ease-in-out;
    box-shadow: 0 1px 25px 0 rgba(0, 0, 0, 0.2);
    border-radius: 50%;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .embed-player .embed-icon i {
      transition: none;
    }
  }
  
  .embed-player .embed-icon i::before {
    margin-right: -2px;
  }
  
  .embed-player .embed-icon i:hover {
    box-shadow: 0 1px 25px 0 rgba(0, 0, 0, 0.2);
    opacity: .85;
  }
  
  .embed-player .embed-icon-dark i {
    color: #fff;
    background-color: #1e1e20;
  }
  
  .embed-player-lg .embed-img {
    height: 600px;
    position: relative;
    display: block;
    background-repeat: no-repeat;
    background-position: 100%;
    background-size: cover;
    border-radius: 0;
  }
  
  .embed-player-lg .embed-img img {
    -o-object-fit: cover;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
  }
  
  .embed-player-lg .embed-icon i {
    width: 90px;
    height: 90px;
    font-size: 26px;
  }