 .site-footer {
    position: relative;
    z-index: 2;
    width: 100%;
    padding: 55px 0 0;
    font-size: 0.9375rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.45);
  }
  
  @media (max-width: 575.98px) {
    .site-footer {
      padding: 35px 5px 0;
    }
  }
  
  .site-footer.bg-dark label,
  .site-footer.bg-dark p,
  .site-footer.bg-dark a {
    color: rgba(255, 255, 255, 0.45);
  }
  
  .site-footer.bg-dark a:hover {
    color: #fff;
  }
  
  .site-footer .custom-control-label::after,
  .site-footer .custom-control-label::before {
    top: 1px;
  }
  
  .site-footer .footer-title {
    margin-bottom: 20px;
    font-size: 0.875rem;
    font-weight: 500;
    color: #fff;
    text-transform: uppercase;
  }
  
  @media (max-width: 575.98px) {
    .site-footer .footer-title {
      margin-bottom: 13px;
    }
  }
  
  .footer-tags a {
    display: inline-block;
    padding: 5px 10px;
    margin: 0 2px 8px 0;
    font-size: 0.9375rem;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 3px;
  }
  
  .footer-tags a:hover {
    text-decoration: none;
    background-color: rgba(255, 255, 255, 0.02);
  }
  
  @media (max-width: 575.98px) {
    .footer-tags a {
      margin: 8px 2px 0 0;
    }
  }
  
  .footer-social {
    margin: 0 -.45rem;
  }
  
  .footer-social a {
    display: inline-block;
    padding: 0 .45rem;
    font-size: 1rem;
    color: rgba(255, 255, 255, 0.8);
    text-align: center;
  }
  
  @media (max-width: 767.98px) {
    .footer-social {
      padding-top: 25px;
      padding-bottom: 15px;
      border-top: 1px solid rgba(255, 255, 255, 0.08);
    }
    .footer-social a {
      /* stylelint-disable-next-line */
      color: rgba(255, 255, 255, 0.9) !important;
    }
  }
  
  .footer-bottom {
    padding-top: 30px;
    padding-bottom: 10px;
    margin-top: 55px;
    border-top: 1px solid rgba(255, 255, 255, 0.05);
  }
  
  @media (max-width: 767.98px) {
    .footer-bottom {
      margin-top: 0;
      border: 0;
    }
  }
  
  .footer-links > a {
    display: inline-block;
    margin: 0 15px 13px 0;
    color: #fff;
  }
  
  .footer-links > a:hover {
    text-decoration: underline;
  }
  
  @media (max-width: 991.98px) {
    .footer-copyright {
      text-align: center;
    }
  }
  
  .sticky-footer .site-content {
    padding-bottom: 415px;
  }
  
  .sticky-footer .site-footer {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 415px;
  }