.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.badge {
  font-size: calc(5px + 2vmin);
  padding: 10px;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.site-header {
    position: relative;
    z-index: 8;
  }


  
/* Fixed Navbar */
.fixed-navbar {
    padding-top: 60px;
  }
  
  .fixed-navbar .site-header {
    position: fixed;
    top: 0;
    z-index: 8;
    width: 100%;
  }
  
  .fixed-navbar .navbar-search {
    position: absolute;
  }
  
  .fixed-navbar .anchor {
    padding-top: 100px;
    margin-top: -100px;
  }
  
  @media (max-width: 991.98px) {
    .fixed-navbar {
      padding-top: 52px;
    }
    .fixed-navbar .anchor {
      padding-top: 0;
      margin-top: 0;
    }
  }

/* Navigation CSS */
@media (max-width: 991.98px) {
    .navbar {
        height: 52px;
        padding: 0;
      }

      .header-mobile {
          margin-top: -5px !important;
      }

      .navbar-toggler {
        z-index: 2;
        height: 52px;
        font-size: 1rem;
        border-right: 1px solid rgba(255, 255, 255, 0.03);
      }
      .navbar-brand {
        position: absolute;
        right: 0 !important;
        width: 100%;
        padding: 0;
        margin: 0;
        font-size: 1.375rem;
        text-align: center;        
      }

      .navbar-search .container {
        padding: 0 10px;
      }
      .navbar-search .search-close {
        right: 10px;
      }
      .navbar-right {
        padding-right: .5rem;
      }
      .navbar-right .nav-link {
        display: flex;
        align-items: center;
        height: 52px;
        padding-right: 7px;
        padding-left: 7px;
      }
      .navbar-collapse {
        position: absolute;
        top: 52px;
        display: block;
        left: 16px;
        width: 100%;
        overflow: auto;
      }
      .navbar-collapse.collapse.show {
        height: 100vh;
        padding-bottom: 52px;
      }
      .navbar-collapse .navbar-nav {
        background-color: #2a2b2d;
        box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
      }
      .navbar-collapse .nav-item .nav-link {
        display: flex;
        align-items: center;
        padding: 12px 17px 12px 13px !important;
        /* stylelint-disable-line */
        border-top: 1px solid rgba(255, 255, 255, 0.03);
      }
      .navbar-collapse .nav-item:last-child .nav-link {
        padding-right: .75rem;
      }

    .show {
        margin-left: -16px !important;
    }
}

@media (max-width: 380px) {
    .navbar-brand {
        width: 70px;
        left: 0;
        margin-left: auto !important;
        margin-right: auto !important;
        font-size: 1.325rem;
        text-align: left;
      }

    .show {
        margin-left: -31px !important;
    }
  }

