.site-header {
    position: relative;
    z-index: 8;
  }


  
/* Fixed Navbar */
.fixed-navbar {
    padding-top: 60px;
  }
  
  .fixed-navbar .site-header {
    position: fixed;
    top: 0;
    z-index: 8;
    width: 100%;
  }
  
  .fixed-navbar .navbar-search {
    position: absolute;
  }
  
  .fixed-navbar .anchor {
    padding-top: 100px;
    margin-top: -100px;
  }
  
  @media (max-width: 991.98px) {
    .fixed-navbar {
      padding-top: 52px;
    }
    .fixed-navbar .anchor {
      padding-top: 0;
      margin-top: 0;
    }
  }

/* Navigation CSS */
@media (max-width: 991.98px) {
    .navbar {
        height: 52px;
        padding: 0;
      }

      .header-mobile {
          margin-top: -5px !important;
      }

      .navbar-toggler {
        z-index: 2;
        height: 52px;
        font-size: 1rem;
        border-right: 1px solid rgba(255, 255, 255, 0.03);
      }
      .navbar-brand {
        position: absolute;
        right: 0 !important;
        width: 100%;
        padding: 0;
        margin: 0;
        font-size: 1.375rem;
        text-align: center;        
      }

      .navbar-search .container {
        padding: 0 10px;
      }
      .navbar-search .search-close {
        right: 10px;
      }
      .navbar-right {
        padding-right: .5rem;
      }
      .navbar-right .nav-link {
        display: flex;
        align-items: center;
        height: 52px;
        padding-right: 7px;
        padding-left: 7px;
      }
      .navbar-collapse {
        position: absolute;
        top: 52px;
        display: block;
        left: 16px;
        width: 100%;
        overflow: auto;
      }
      .navbar-collapse.collapse.show {
        height: 100vh;
        padding-bottom: 52px;
      }
      .navbar-collapse .navbar-nav {
        background-color: #2a2b2d;
        box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
      }
      .navbar-collapse .nav-item .nav-link {
        display: flex;
        align-items: center;
        padding: 12px 17px 12px 13px !important;
        /* stylelint-disable-line */
        border-top: 1px solid rgba(255, 255, 255, 0.03);
      }
      .navbar-collapse .nav-item:last-child .nav-link {
        padding-right: .75rem;
      }

    .show {
        margin-left: -16px !important;
    }
}

@media (max-width: 380px) {
    .navbar-brand {
        width: 70px;
        left: 0;
        margin-left: auto !important;
        margin-right: auto !important;
        font-size: 1.325rem;
        text-align: left;
      }

    .show {
        margin-left: -31px !important;
    }
  }

.nav {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
  }
  
  .nav-link {
    display: block;
    padding: 0.625rem 1rem;
  }
  
  .nav-link:hover, .nav-link:focus {
    text-decoration: none;
  }
  
  .nav-fill .nav-item {
    flex: 1 1 auto;
    text-align: center;
  }
  
  .nav-justified .nav-item {
    flex-basis: 0;
    flex-grow: 1;
    text-align: center;
  }
  
  .navbar {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 1rem;
  }
  
  .navbar > .container,
  .navbar > .container-fluid {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
  
  .navbar-brand {
    display: inline-block;
    padding-top: 0.4375rem;
    padding-bottom: 0.4375rem;
    margin-left: auto !important;
    margin-right: auto !important;
    font-size: 1.25rem;
    line-height: inherit;
    white-space: nowrap;
    font-weight: 700;
  }
  
  .navbar-brand:hover, .navbar-brand:focus {
    text-decoration: none;
  }
  
  .navbar-nav {
    display: flex;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
  }
  
  .navbar-nav .nav-link {
    padding-right: 0;
    padding-left: 0;
  }
  
  .navbar-text {
    display: inline-block;
    padding-top: 0.625rem;
    padding-bottom: 0.625rem;
  }
  
  .navbar-collapse {
    flex-basis: 100%;
    flex-grow: 1;
    align-items: center;
  }
  
  .navbar-toggler {
    padding: 0.25rem 0.75rem;
    font-size: 1.25rem;
    line-height: 1;
    background-color: transparent;
    border: 1px solid transparent;
    border-radius: 0.25rem;
  }
  
  .navbar-toggler:hover, .navbar-toggler:focus {
    text-decoration: none;
  }
  
  .navbar-toggler-icon {
    display: inline-block;
    width: 1.5em;
    height: 1.5em;
    vertical-align: middle;
    content: "";
    background: no-repeat center center;
    background-size: 100% 100%;
  }

  .navbar-search {
    position: absolute;
    top: -60px;
    left: 0;
    z-index: -1;
    width: 100%;
    padding-bottom: 10px;
    background-color: #2a2b2d;
    transition: all 0.2s ease-in-out;
  }
  
  @media (min-width: 992px) {
    .navbar-expand-lg {
      flex-flow: row nowrap;
      justify-content: flex-start;
    }
    .navbar-expand-lg .navbar-nav {
      flex-direction: row;
    }
    .navbar-expand-lg .navbar-nav .nav-link {
      padding-right: 10px;
      padding-left: 10px;
    }
    .navbar-expand-lg > .container,
    .navbar-expand-lg > .container-fluid {
      flex-wrap: nowrap;
    }
    .navbar-expand-lg .navbar-collapse {
      display: flex !important;
      flex-basis: auto;
    }
    .navbar-expand-lg .navbar-toggler {
      display: none;
    }
  }
  
  @media (max-width: 1199.98px) {
    .navbar-expand-xl > .container,
    .navbar-expand-xl > .container-fluid {
      padding-right: 0;
      padding-left: 0;
    }
  }
  
  .navbar-expand {
    flex-flow: row nowrap;
    justify-content: flex-start;
  }
  
  .navbar-expand > .container,
  .navbar-expand > .container-fluid {
    padding-right: 0;
    padding-left: 0;
  }
  
  .navbar-expand .navbar-nav {
    flex-direction: row;
  }
  
  .navbar-expand .navbar-nav .nav-link {
    padding-right: 10px;
    padding-left: 10px;
  }
  
  .navbar-expand > .container,
  .navbar-expand > .container-fluid {
    flex-wrap: nowrap;
  }
  
  .navbar-expand .navbar-collapse {
    display: flex !important;
    flex-basis: auto;
  }
  
  .navbar-expand .navbar-toggler {
    display: none;
  }
  
  .navbar-dark .navbar-brand {
    color: #fff;
  }
  
  .navbar-dark .navbar-brand:hover, .navbar-dark .navbar-brand:focus {
    color: #fff;
  }
  
  .navbar-dark .navbar-nav .nav-link {
    color: rgba(255, 255, 255, 0.8);
  }
  
  .navbar-dark .navbar-nav .nav-link:hover, .navbar-dark .navbar-nav .nav-link:focus {
    color: #fff;
  }
  
  .navbar-dark .navbar-nav .nav-link.disabled {
    color: rgba(255, 255, 255, 0.25);
  }
  
  .navbar-dark .navbar-nav .show > .nav-link,
  .navbar-dark .navbar-nav .active > .nav-link,
  .navbar-dark .navbar-nav .nav-link.show,
  .navbar-dark .navbar-nav .nav-link.active {
    color: #fff;
  }
  
  .navbar-dark .navbar-toggler {
    color: rgba(255, 255, 255, 0.8);
    border-color: transparent;
  }
  
  .navbar-dark .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='rgba(255, 255, 255, 0.8)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
  }
  
  .navbar-dark .navbar-text {
    color: rgba(255, 255, 255, 0.8);
  }
  
  .navbar-dark .navbar-text a {
    color: #fff;
  }
  
  .navbar-dark .navbar-text a:hover, .navbar-dark .navbar-text a:focus {
    color: #fff;
  }

  @media (prefers-reduced-motion: reduce) {
    .navbar-search {
      transition: none;
    }
  }
  
  .navbar-search .container {
    position: relative;
  }
  
  .navbar-search.active {
    top: 60px;
  }
  
  @media (max-width: 991.98px) {
    .navbar-search.active {
      top: 52px;
    }
  }
  
  .navbar-search .form-control {
    border-color: transparent;
  }
  
  .navbar-search .form-control:focus {
    border-color: transparent;
    box-shadow: none;
  }
  
  .navbar-search .search-close {
    position: absolute;
    top: 0;
    right: 15px;
    width: 38px;
    height: 38px;
    line-height: 38px;
    text-align: center;
    cursor: pointer;
  }
  
  .navbar-search .search-close:hover {
    color: #333333;
  }
  
  .nav-list {
    margin: 0 -5px;
  }
  
  .nav-list .nav-link {
    padding: 19px 7px;
    margin: 0 5px;
    transition: all 0.2s ease-in-out;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .nav-list .nav-link {
      transition: none;
    }
  }
  
  .nav-list .nav-link:hover, .nav-list .nav-link:active, .nav-list .nav-link:focus, .nav-list .nav-link.active {
    font-weight: 500;
    color: #2575dc;
    box-shadow: 0 3px 0 0 #2575dc;
  }
  
  .nav-list .nav-link.active {
    font-weight: 500;
  }
  
  @media (max-width: 991.98px) {
    .nav-list .nav-link {
      padding: 15px 12px;
      margin: 0;
    }
  }

.navbar-nav .nav-link {
   position: relative;
   font-weight: 500;
 }


/* Search Form */
.form-control {
    display: block;
    width: 100%;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #55585d;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #d9d8de;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .form-control {
      transition: none;
    }
  }
  
  .form-control::-ms-expand {
    background-color: transparent;
    border: 0;
  }
  
  .form-control:focus {
    color: #55585d;
    background-color: #fff;
    border-color: #93bbee;
    outline: 0;
    box-shadow: 0 0 0 0.2rem #d5e4f8;
  }
  
  .form-control::-webkit-input-placeholder {
    color: #6c757d;
    opacity: 1;
  }
  
  .form-control::-moz-placeholder {
    color: #6c757d;
    opacity: 1;
  }
  
  .form-control:-ms-input-placeholder {
    color: #6c757d;
    opacity: 1;
  }
  
  .form-control::-ms-input-placeholder {
    color: #6c757d;
    opacity: 1;
  }
  
  .form-control::placeholder {
    color: #6c757d;
    opacity: 1;
  }

  .form-control-lg {
    font-size: 18px;
  }
  
  .form-box {
    overflow: auto;
  }
  
  .form-control-inline {
    min-width: 25rem;
  }
  
  @media (max-width: 1199.98px) {
    .form-control-inline {
      min-width: 2rem;
    }
  }
  
  @media (max-width: 991.98px) {
    .form-control-inline {
      min-width: auto;
    }
  }