/*
  *  Owl Carousel - Core
  */
  .owl-carousel {
    display: none;
    width: 100%;
    -webkit-tap-highlight-color: transparent;
    /* position relative and z-index fix webkit rendering fonts issue */
    position: relative;
    z-index: 1;
  }
  
  .owl-carousel .owl-stage {
    position: relative;
    touch-action: manipulation;
    -moz-backface-visibility: hidden;
    /* fix firefox animation glitch */
  }
  
  .owl-carousel .owl-stage:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
  }
  
  .owl-carousel .owl-stage-outer {
    position: relative;
    overflow: hidden;
    /* fix for flashing background */
    -webkit-transform: translate3d(0px, 0px, 0px);
  }
  
  .owl-carousel .owl-wrapper,
  .owl-carousel .owl-item {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
  }
  
  .owl-carousel .owl-item {
    position: relative;
    min-height: 1px;
    float: left;
    -webkit-backface-visibility: hidden;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
  }
  
  .owl-carousel .owl-item img {
    display: block;
    width: 100%;
  }
  
  .owl-carousel .owl-nav.disabled,
  .owl-carousel .owl-dots.disabled {
    display: none;
  }
  
  .owl-carousel .owl-nav .owl-prev,
  .owl-carousel .owl-nav .owl-next,
  .owl-carousel .owl-dot {
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  .owl-carousel .owl-nav button.owl-prev,
  .owl-carousel .owl-nav button.owl-next,
  .owl-carousel button.owl-dot {
    background: none;
    color: inherit;
    border: none;
    padding: 0 !important;
    font: inherit;
  }
  
  .owl-carousel.owl-loaded {
    display: block;
  }
  
  .owl-carousel.owl-loading {
    opacity: 0;
    display: block;
  }
  
  .owl-carousel.owl-hidden {
    opacity: 0;
  }
  
  .owl-carousel.owl-refresh .owl-item {
    visibility: hidden;
  }
  
  .owl-carousel.owl-drag .owl-item {
    touch-action: pan-y;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  .owl-carousel.owl-grab {
    cursor: move;
    cursor: -webkit-grab;
    cursor: grab;
  }
  
  .owl-carousel.owl-rtl {
    direction: rtl;
  }
  
  .owl-carousel.owl-rtl .owl-item {
    float: right;
  }

.owl-carousel-list {
    position: relative;
    display: block;
  }
  
  .owl-caption {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 25px;
    text-align: center;
  }
  
  .owl-caption .owl-title {
    font-size: 2.8125rem;
    font-weight: 900;
    color: #fff;
  }
  
  @media (max-width: 575.98px) {
    .owl-caption .owl-title {
      font-size: 2.325rem;
    }
  }
  
  .owl-caption .owl-text {
    margin: 10px 0 50px;
    font-size: 1.125rem;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.7);
  }
  
  @media (max-width: 575.98px) {
    .owl-caption .owl-text {
      font-size: 1rem;
    }
  }
  
  @media (max-width: 991.98px) {
    .owl-caption .owl-text br {
      display: none;
    }
  }
  
  .owl-caption-bottom {
    top: auto;
    bottom: 0;
    justify-content: start;
    height: auto;
    padding: 0 25px 0 0;
    text-align: left;
  }
  
  .owl-caption-bottom .owl-title {
    padding: 15px 20px 10px 5px;
    margin: 0;
    font-size: 1.375rem;
    font-weight: 700;
    color: #373737;
    background-color: #fafafa;
  }
  
  @media (max-width: 991.98px) {
    .owl-caption-bottom .owl-title {
      padding-left: 15px;
      font-size: 1.275rem;
    }
  }
  
  .owl-height-100 {
    background-color: #313133;
  }
  
  .owl-height-100,
  .owl-height-100 .owl-item,
  .owl-height-100 .owl-carousel-item {
    height: calc(100vh - 60px);
  }
  
  @media (max-width: 991.98px) {
    .owl-height-100,
    .owl-height-100 .owl-item,
    .owl-height-100 .owl-carousel-item {
      height: calc(100vh - 52px);
    }
  }
  
  .owl-height-100 .owl-caption-container {
    width: 50%;
  }
  
  @media (max-width: 991.98px) {
    .owl-height-100 .owl-caption-container {
      width: 100%;
    }
  }
  
  .owl-height-100 img {
    position: absolute;
    -o-object-fit: cover;
    object-fit: cover;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    opacity: .25;
  }
  
  .owl-carousel-theme {
    position: relative;
  }
  
  .owl-carousel-theme.owl-grab {
    cursor: pointer;
  }
  
  .owl-carousel-theme .owl-dots {
    position: absolute;
    right: 0;
    bottom: 45px;
    left: 0;
    z-index: 3;
    margin: 0 auto;
    text-align: center;
  }
  
  .owl-carousel-theme .owl-dots .owl-dot {
    display: inline-block;
    width: 16px;
    height: 16px;
    margin: 0 5px;
    background-color: #fff;
    opacity: .5;
    border-radius: 50%;
    transition: all 0.2s ease-in-out;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .owl-carousel-theme .owl-dots .owl-dot {
      transition: none;
    }
  }
  
  .owl-carousel-theme .owl-dots .owl-dot.active {
    opacity: 1;
    -webkit-transform: scale(1.4);
    transform: scale(1.4);
  }
  
  @media (max-width: 767.98px) {
    .owl-carousel-theme .owl-dots {
      bottom: 25px;
    }
  }
  
  .owl-nav button {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10%;
    height: 100%;
    font-size: 0;
    color: #fff !important;
    opacity: .2;
  }
  
  .owl-nav button:hover {
    opacity: 1;
  }
  
  .owl-nav button span {
    display: none;
  }
  
  .owl-nav .owl-prev::after,
  .owl-nav .owl-next::after {
    font-family: "nucleo";
    font-size: 2.125rem;
  }
  
  .owl-nav .owl-prev::after {
    content: "\E007";
  }
  
  .owl-nav .owl-next {
    right: 0;
    left: auto;
  }
  
  .owl-nav .owl-next::after {
    content: "\E008";
  }
  
  @media (max-width: 767.98px) {
    .owl-nav {
      display: none;
    }
  }
  
  .owl-img-md {
    height: 650px;
    background-color: #2a2b2d;
    position: relative;
    display: block;
    background-repeat: no-repeat;
    background-position: 100%;
    background-size: cover;
    border-radius: 0;
  }
  
  .owl-img-md img {
    -o-object-fit: cover;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
  }
  
  @media (max-width: 767.98px) {
    .owl-img-md {
      height: 500px;
    }
  }
  
  .owl-img-games {
    width: 450px;
    height: 700px;
    background-color: #2a2b2d;
    position: relative;
    display: block;
    background-repeat: no-repeat;
    background-position: 100%;
    background-size: cover;
    border-radius: 0;
  }
  
  .owl-img-games img {
    -o-object-fit: cover;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
  }
  
  @media (max-width: 767.98px) {
    .owl-img-games {
      height: 500px;
    }
  }
  
  .owl-width-md {
    width: 800px;
  }
  
  @media (max-width: 767.98px) {
    .owl-width-md {
      width: 100%;
      margin-left: -1px;
    }
  }
  
  .owl-carousel-centered .owl-stage {
    display: flex;
    align-items: center;
  }
  
  .owl-carousel {
    /* stylelint-disable-next-line */
  }
  
  .owl-carousel .animated {
    -webkit-animation-duration: 1000ms;
    animation-duration: 1000ms;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }
  
  .owl-carousel .owl-animated-in {
    z-index: 0;
  }
  
  .owl-carousel .owl-animated-out {
    z-index: 1;
  }
  
  .owl-carousel .fadeOut {
    -webkit-animation-name: fadeOut;
    animation-name: fadeOut;
  }
  
  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  
  .owl-height {
    transition: height 500ms ease-in-out;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .owl-height {
      transition: none;
    }
  }

  /* Carousel More Stuff */


 
 .carousel {
    position: relative;
  }
  
  .carousel.pointer-event {
    touch-action: pan-y;
  }
  
  .carousel-inner {
    position: relative;
    width: 100%;
    overflow: hidden;
  }
  
  .carousel-inner::after {
    display: block;
    clear: both;
    content: "";
  }
  
  .carousel-item {
    position: relative;
    display: none;
    float: left;
    width: 100%;
    margin-right: -100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transition: -webkit-transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .carousel-item {
      transition: none;
    }
  }
  
  .carousel-item.active,
  .carousel-item-next,
  .carousel-item-prev {
    display: block;
  }
  
  .carousel-item-next:not(.carousel-item-left),
  .active.carousel-item-right {
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
  }
  
  .carousel-item-prev:not(.carousel-item-right),
  .active.carousel-item-left {
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
  
  .carousel-fade .carousel-item {
    opacity: 0;
    transition-property: opacity;
    -webkit-transform: none;
    transform: none;
  }
  
  .carousel-fade .carousel-item.active,
  .carousel-fade .carousel-item-next.carousel-item-left,
  .carousel-fade .carousel-item-prev.carousel-item-right {
    z-index: 1;
    opacity: 1;
  }
  
  .carousel-fade .active.carousel-item-left,
  .carousel-fade .active.carousel-item-right {
    z-index: 0;
    opacity: 0;
    transition: 0s 0.6s opacity;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .carousel-fade .active.carousel-item-left,
    .carousel-fade .active.carousel-item-right {
      transition: none;
    }
  }
  
  .carousel-control-prev,
  .carousel-control-next {
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 15%;
    color: #fff;
    text-align: center;
    opacity: 0.5;
    transition: opacity 0.15s ease;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .carousel-control-prev,
    .carousel-control-next {
      transition: none;
    }
  }
  
  .carousel-control-prev:hover, .carousel-control-prev:focus,
  .carousel-control-next:hover,
  .carousel-control-next:focus {
    color: #fff;
    text-decoration: none;
    outline: 0;
    opacity: 0.9;
  }
  
  .carousel-control-prev {
    left: 0;
  }
  
  .carousel-control-next {
    right: 0;
  }
  
  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: no-repeat 50% / 100% 100%;
  }
  
  .carousel-control-prev-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3e%3c/svg%3e");
  }
  
  .carousel-control-next-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3e%3cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3e%3c/svg%3e");
  }
  
  .carousel-indicators {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 15;
    display: flex;
    justify-content: center;
    padding-left: 0;
    margin-right: 15%;
    margin-left: 15%;
    list-style: none;
  }
  
  .carousel-indicators li {
    box-sizing: content-box;
    flex: 0 1 auto;
    width: 30px;
    height: 3px;
    margin-right: 3px;
    margin-left: 3px;
    text-indent: -999px;
    cursor: pointer;
    background-color: #fff;
    background-clip: padding-box;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    opacity: .5;
    transition: opacity 0.6s ease;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .carousel-indicators li {
      transition: none;
    }
  }
  
  .carousel-indicators .active {
    opacity: 1;
  }
  
  .carousel-caption {
    position: absolute;
    right: 15%;
    bottom: 20px;
    left: 15%;
    z-index: 10;
    padding-top: 20px;
    padding-bottom: 20px;
    color: #fff;
    text-align: center;
  }